<template>
    <!-- <div class="view-container"> -->
        <div id="viewDiv"></div>
        <!-- <div class="infoDiv">
            <el-button round @click="switchView">关闭</el-button>
        </div> -->
    <!-- </div> -->
</template>
<script>

export default {
    name: "baseMap",
    components: {
    },
    mounted() {

    },
    beforeMount() {
        this.loadView();
    },
    methods: {
        loadView() {
            return new Promise((resolve, reject) => {
                this.$loadModules([ "esri/Map","esri/views/SceneView","esri/views/MapView"])
                .then(([Map,SceneView,MapView]) => {
                    let map = new Map({
                        basemap: "streets-night-vector",
                        ground: "world-elevation",
                    });
                   
                    const mapView = new MapView({
                        zoom: 6,
                        container: "viewDiv",
                        center: [113.257, 35.173, 931],
                        map: map
                    });
                    let sceneView = new SceneView({
                        map: map,
                        container: "viewDiv",
                        camera: {
                            position: [113.257, 35.173, 931],
                            heading: 356.073,
                            tilt: 51.614,
                        },
                    });
                    let activeView = sceneView;
                    ArcCIM.view = activeView;
                    // 触发自定义事件
                    this.$emit("mapLoaded"); 
                    // 在生成地图完成后执行 resolve,通知 Promise已经完成
                    resolve(activeView); 
                }).catch((err) => {
                    reject(err);
                })
            })
        },
        switchView(){

        }
    }
}
</script>
